:root {
    /*Colors*/

    --neutral50: #fafafa;
    --neutral100: #f5f5f5;
    --neutral200: #e5e5e5;
    --neutral300: #d4d4d4;
    --neutral400: #a3a3a3;
    --neutral500: #737373;
    --neutral600: #525252;
    --neutral700: #404040;
    --neutral800: #262626;
    --neutral900: #171717;

    --red200: #fecaca;
    --red300: #fda4af;
    --red400: #f87171;

    --green400: #4ade80;
    --green200: #bbf7d0;

    --fuchsia400: #e879f9;

    --indigo300: #a5b4fc;
    --indigo400: #818cf8;
    --indigo500: #6366f1;
}

*,
::before,
::after {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

/* for custom scrollbar for webkit browser*/

::-webkit-scrollbar {
    width: 6px;
}
::-webkit-scrollbar-track {
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}
::-webkit-scrollbar-thumb {
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

body {
    font-family: "Sen", sans-serif;
    font-size: 16px;
    font-weight: 400;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

/* Generals */
code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
        monospace;
}

button {
    font-family: "Sen", sans-serif;
    cursor: pointer;
    background: none;
    border: none;
    outline: none;
}
a {
    text-decoration: none;
    color: var(--indigo400);
}

a:hover {
    color: var(--indigo300);
}

input {
    font-family: "Sen", sans-serif;
    font-size: 16px;
    border: none;
    outline: none;
}

input:focus {
    border: none;
    outline: none;
}

ul {
    list-style: none;
}

.container {
    max-width: 1200px;
    margin: 0 auto;
}

.card {
    width: 100%;
    background-color: var(--neutral800);
    color: var(--neutral200);
    border-radius: 20px;
    padding: 30px 40px;
    margin-bottom: 30px;
    box-shadow: 3px 3px 6px 0 rgba(0, 0, 0, 0.15);
}

.btn {
    font-size: 14px;
    font-weight: 700;
    color: var(--neutral50);
    background-color: var(--indigo400);
    border-radius: 5px;
    padding: 8px 15px;
    transition: all 0.3s ease;
}

.btn:hover {
    background-color: var(--indigo500);
}

.btn:disabled {
    cursor: default;
    color: var(--neutral400);
    background-color: var(--neutral800);
}

/* .btn_disabled:hover{
color: var(--neutral200);
background-color: var(--neutral600);
} */

/* Header */

.header {
    width: 100%;
    height: 110px;
    background-color: var(--neutral50);
}

.header_box {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.header_box > .header_logo {
    font-size: 28px;
    font-weight: 800;
    color: var(--neutral700);
}

.header_box > .header_logo > span {
    color: var(--indigo500);
}

/* Main */

.main {
    padding-top: 50px;
}

.main_box {
    max-width: 700px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0 25px;
    margin: 0 auto;
}

.about-link {
    position: fixed;
    bottom: 30px;
    right: 30px;
}

/* Stats */
/* main_box > stats */

.stats {
    width: 100%;
    margin-bottom: 50px;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.stats .stats_rating span {
    font-size: 20px;
    font-weight: 800;
    margin-right: 12px;
}

.stats .stats_review {
    color: var(--neutral400);
}

/* Feedback card */
/* main_box > feedback_wrapper */

.feedback_wrapper {
    width: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
}

.feedback_item {
    position: relative;
}

.feedback_reverse {
    background-color: var(--neutral50);
    color: var(--neutral800);
}

.feedback_rating {
    position: absolute;
    width: 40px;
    height: 40px;
    display: grid;
    place-items: center;
    border-radius: 100%;
    background-color: var(--indigo500);
    color: #fff;
    font-weight: 700;
    top: -10px;
    left: -10px;
}

.feedback_item button {
    position: absolute;
    top: 15px;
    right: 15px;
    font-size: 20px;
    transition: color 0.3s ease;
}

.feedback_item .feedback_btnDelete {
    color: var(--green400);
}

.feedback_item .feedback_btnDelete :hover {
    color: var(--red400);
}

.feedback_item .feedback_btnEdit {
    color: var(--neutral200);
    margin-right: 30px;
}

.feedback_item .feedback_btnEdit :hover {
    color: var(--indigo400);
}

.feedback_text {
    font-weight: 400;
}

/* Form card */

.form_card > form {
    text-align: center;
    /* height: 115px;
margin-top: 15px; */
}

.form_card > form > h3 {
    margin-bottom: 20px;
}

.form_inputGroup {
    display: flex;
    flex-direction: row;
    overflow: hidden;
    border-radius: 15px;
    background-color: var(--neutral700);
    padding: 10px;
}

.form_inputGroup > input {
    height: 35px;
    flex-grow: 1;
    color: var(--neutral50);
    background-color: var(--neutral700);
    padding: 0 20px 0 10px;
}

.form_inputGroup > button > span {
    position: relative;
    top: 3px;
}

.form_message {
    font-size: 14px;
    color: var(--red300);
    margin-top: 20px;
}

/* Rating select */

.rating {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    padding: 0 10px;
    margin-bottom: 20px;
}

.rating > li {
    position: relative;
    width: 50px;
    height: 50px;
    margin: 10px;
}

.rating > li > label {
    cursor: pointer;
    position: absolute;
    top: 0;
    display: grid;
    place-items: center;
    width: 50px;
    height: 50px;
    background-color: var(--neutral700);
    color: var(--neutral400);
    border-radius: 50%;
    font-weight: 700;
    transition: 0.3s;
}

.rating > li > label:hover {
    background: var(--indigo300);
    color: #fff;
}

[type="radio"] {
    visibility: hidden;
}

[type="radio"]:checked ~ label {
    background: var(--indigo500);
    color: #fff;
}

/* About page */
.about h1 {
    font-size: 25px;
    margin-bottom: 20px;
}

.about p {
    margin-bottom: 5px;
}

.about p:last-of-type {
    margin-bottom: 20px;
}
